import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-manage-supplier',
  templateUrl: './manage-supplier.component.html',
  styleUrls: ['./manage-supplier.component.scss'],
})
export class ManageSupplierComponent implements OnInit {
  private dataUrl = 'assets/data/country.json';
  arrSuppliers: any = [];

  arrAllSuppliers: any = [];
  arrCountries: any[] = [];
  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private supplierService: SupplierService,
    private formBuilder: FormBuilder,
    private readonly http: HttpClient,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      supplier: [null],
      customer: [''],
      customerCode: [''],
      country: [null],
    });
  }

  ngOnInit() {
    this.getCountryData();
    this.getAllSuppliers();
    this.getAllSuppliersAll();
  }
  getCountryData() {
    this.http.get(this.dataUrl).subscribe((data: any) => {
      this.arrCountries = data;
    });
  }
  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllSuppliers();
  }
  onSupplierChange() {
    const customer = this.arrAllSuppliers.find((e) => {
      return e.sup_Code === this.myForm.value.supplier;
    });

    this.myForm.patchValue({
      customer: customer.customer_Name,
      customerCode: customer.cust_Code,
    });
  }
  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) +
      this.arrSuppliers.length
    );
  }
  getAllSuppliers() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      fkSupplierId: '',
      cust_Code: this.myForm.value.customerCode,
      sup_Code: this.myForm.value.supplier,
      strCountry: this.myForm.value.country,
    };

    this.fetchLoading = true;
    this.supplierService.getAllSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrSuppliers = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrSuppliers = [];
        // alert(res.message);
      }
    });
  }
  getAllSuppliersAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 100000,
    };

    this.fetchLoading = true;
    this.supplierService.getAllSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrAllSuppliers = res.data.map((d) => {
          return {
            ...d,
            displayName: d.sup_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllSuppliers = [];
      }
    });
  }
  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      sup_Code: this.myForm.value.supplier,
      strCountry: this.myForm.value.country,
    };

    this.fetchLoading = true;
    this.supplierService.getAllSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          let obj: any = {
            'Supplier Code': e.sup_Code,
            'Supplier Name': e.strName,
            Customer: e.customer_Name,
            Package: e.strPackage,
            Amenities: e.Amenities,
            Amount: e.Amount,
            'Paid Amount': e.Total_Paid_Amount,
            'Balance Amount': e.Amount - e.Total_Paid_Amount,
            Refund: e.Refund_Amount,
            'G-Voucher Credit': e.Service_Credit_Amount,
            'G-Voucher Debit': e.Service_Debit_Amount,
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(jsonData, 'Suppliers ');
      }
    });
  }

  deleteSupplier(id) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#d33',
      reverseButtons: true,
      confirmButtonText: 'Yes, delete it!',
    }).then((result) => {
      if (result.isConfirmed) {
        const obj = {
          strLoginUserId: localStorage.getItem('userId'),
          fkSupplierId: id,
        };

        this.supplierService.deleteSupplier(obj).subscribe((res) => {
          if (res.success) {
            Swal.fire({
              title: 'Deleted!',
              text: 'Your file has been deleted.',
              icon: 'success',
            });
            this.getAllSuppliers();
          } else {
            // alert(res.message);
          }
        });
      }
    });
  }
}
