import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-update-supplier',
  templateUrl: './update-supplier.component.html',
  styleUrls: ['./update-supplier.component.scss'],
})
export class UpdateSupplierComponent {
  private dataUrl = 'assets/data/country.json';

  arrCountries: any[] = [];

  myForm: FormGroup;
  submitted: boolean = false;
  supplierId = '';
  fetchLoading = false;
  arrCustomer: any = [];

  constructor(
    private activatedRoute: ActivatedRoute,
    private supplierService: SupplierService,
    private router: Router,
    private readonly http: HttpClient,
    private formBuilder: FormBuilder
  ) {
    activatedRoute.params.subscribe((params) => {
      this.supplierId = params.id;
    });

    this.myForm = this.formBuilder.group({
      strName: ['', Validators.required],
      phone: ['', Validators.required],
      strCountry: [null, Validators.required],
      Amenities: ['', Validators.required],
      Amount: [0, Validators.required],
      Refund_Amount: [0, Validators.required],
      Service_Credit_Amount: [0, Validators.required],
      Service_Debit_Amount: [0, Validators.required],
    });
    this.getCountryData();
  }

  getCountryData() {
    this.http.get(this.dataUrl).subscribe((data: any) => {
      this.arrCountries = data;
      this.getAllSupplier();
    });
  }

  get f() {
    return this.myForm.controls;
  }

  getAllSupplier() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1,
      fkSupplierId: this.supplierId,
    };

    this.fetchLoading = true;
    this.supplierService.getAllSupplier(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data[0];
        this.patchValue(this.arrCustomer);
      } else {
        this.arrCustomer = [];
      }
    });
  }

  patchValue(customer) {
    this.myForm.patchValue({
      strName: customer.strName,
      phone: customer.phone,
      strCountry: customer.country,
      Amenities: customer.Amenities,
      Amount: customer.Amount,
      Refund_Amount: customer.Refund_Amount,
      Service_Credit_Amount: customer.Service_Credit_Amount,
      Service_Debit_Amount: customer.Service_Debit_Amount,
    });
  }

  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.invalid) {
      return;
    }

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkSupplierId: this.supplierId,
      strName: this.myForm.value.strName,
      phone: this.myForm.value.phone,
      country: this.myForm.value.strCountry,
      Amenities: this.myForm.value.Amenities,
      Amount: this.myForm.value.Amount,
    };

    this.supplierService.updateSupplier(obj).subscribe((res) => {
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully updated Supplier',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.submitted = false;
            this.router.navigate(['/supplier']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
