import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgbTooltipModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { WidgetsModule } from 'src/app/theme/partials';
import { CreateSupplierComponent } from './create-supplier/create-supplier.component';
import { ManageSupplierComponent } from './manage-supplier/manage-supplier.component';
import { UpdateSupplierComponent } from './update-supplier/update-supplier.component';

@NgModule({
  declarations: [
    ManageSupplierComponent,
    CreateSupplierComponent,
    UpdateSupplierComponent,
  ],
  imports: [
    CommonModule,
    NgbTooltipModule,
    FormsModule,
    NgSelectModule,
    InlineSVGModule,
    ReactiveFormsModule,
    TranslateModule,
    RouterModule.forChild([
      {
        path: '',
        component: ManageSupplierComponent,
      },
      {
        path: 'create',
        component: CreateSupplierComponent,
      },
      {
        path: 'update/:id',
        component: UpdateSupplierComponent,
      },
    ]),
    WidgetsModule,
  ],
})
export class SupplierModule {}
