import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-create-supplier',
  templateUrl: './create-supplier.component.html',
  styleUrls: ['./create-supplier.component.scss'],
})
export class CreateSupplierComponent {
  private dataUrl = 'assets/data/country.json';

  arrCountries: any[] = [];
  arrAmenities: any[] = [];

  myForm: FormGroup;
  submitted: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private supplierService: SupplierService,
    private readonly http: HttpClient,
    private router: Router
  ) {
    this.myForm = this.formBuilder.group({
      strName: ['', Validators.required],
      phone: ['', Validators.required],
      strCountry: [null, Validators.required],
      Amenities: [null, Validators.required],
      Amount: ['', Validators.required],
    });
    this.getCountryData();
    this.getMetaData();
  }

  get f() {
    return this.myForm.controls;
  }

  getMetaData() {
    const obj = {
      metaType: 'Amentities', //Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrAmenities = data.data;
      } else {
        this.arrAmenities = [];
      }
    });
  }

  getCountryData() {
    this.http.get(this.dataUrl).subscribe((data: any) => {
      this.arrCountries = data;
    });
  }

  resetForm() {
    this.myForm.reset();
    this.submitted = false;
  }
  onSubmit() {
    this.submitted = true;

    if (this.myForm.invalid) {
      return;
    }

    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      strName: this.myForm.value.strName,
      phone: this.myForm.value.phone,
      country: this.myForm.value.strCountry,
      Amenities: this.myForm.value.Amenities,
      Amount: this.myForm.value.Amount,
    };

    this.supplierService.addSupplier(obj).subscribe((res) => {
      if (res.success) {
        Swal.fire({
          title: 'Success',
          text: 'Successfully Created Supplier',
          icon: 'success',
        }).then((value) => {
          if (value) {
            this.myForm.reset();
            this.submitted = false;
            this.router.navigate(['/supplier']);
          }
        });
      } else {
        Swal.fire({
          title: 'Error',
          text: res.message,
          icon: 'error',
        });
      }
    });
  }
}
